@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');
body {
    margin: 0;
    padding: 0;
    width: 100vw;
    font-family: 'Lato', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.App {
    margin: 0;
    width: 100%;
    padding: 0;
    overflow: hidden;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

a {
    cursor: pointer;
    text-decoration: none;
    display: block;
}

html {
    transition: all 0.2s ease-in;
}

.register {
    justify-content: center;
}

.redirectbtn {
    background-color: #f6753d !important;
    padding: 14px 32px;
    color: #ffffff;
    outline: none;
    border: none;
    font-size: 1.2rem;
    border-radius: 20px;
    font-weight: bold;
    display: block;
    margin: 40px auto auto auto;
}

.redirectbtn:hover {
    box-shadow: 0 3px 6px #f6753d;
    cursor: pointer;
}

@media screen and (min-width: 600px) {
    .dialogue {
        min-width: 535px;
    }
}

.team {
    display: flex;
    justify-content: center;
    background-color: #051640;
    flex-wrap: wrap;
}

.typo {
    margin: 30px 0 5px 0 !important;
}

.one {
    display: block;
    width: 80%;
}

.two {
    display: block;
    width: 40%;
}

.three {
    display: block;
    width: 26%;
}

.bmember {
    margin: auto;
    width: fit-content;
    padding: 10px;
}

.name {
    color: #ffffff;
    margin-bottom: 0px;
    text-align: center;
    font-size: 1.8rem;
    opacity: 1;
}

.desig {
    margin-top: 2px;
    text-align: center;
    font-size: 1.5rem;
    letter-spacing: 0px;
    color: #f6753d;
    text-transform: capitalize;
    opacity: 1;
    padding-bottom: 20px;
}

@media (max-width: 991px) {
    .name {
        font-size: 1.5rem;
    }
    .desig {
        font-size: 1.3rem;
    }
    .three {
        width: 40%;
    }
}

@media (max-width: 650px) {
    .one,
    .two,
    .three {
        width: 95%;
    }
}

.flip-box {
    background-color: transparent;
    padding-top: 10px;
    margin-right: auto;
    margin-left: auto;
    overflow: hidden;
    display: block;
    width: 200px;
    height: 200px;
    border-radius: 15px 50px;
}

.flip-box-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
}

.flip-box:hover .flip-box-back {
    opacity: 1;
}

.flip-box-front,
.flip-box-back {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 15px 50px;
}

.flip-box-front {
    background-color: #bbb;
    color: black;
}

.flip-box-front img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 15px 50px;
}

.flip-box-back {
    background-color: #0077b5aa;
    font-size: 64px;
    opacity: 0;
    transition: all 0.3s ease;
}

.flip-box-back a {
    display: flex;
    height: 100%;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: #fff;
}

.background {
    background-color: #000000;
    background-image: url('./final_cover.png');
    filter: blur(2px);
    -webkit-filter: blur(2px);
    background-size: cover;
    background-position: center;
    min-height: 100vh;
}

.overlay {
    position: absolute;
    background-color: #2f312f;
    filter: opacity(68%);
    -webkit-filter: opacity(68%);
    top: 0px;
    min-height: 100vh;
    min-width: 100vw;
}

.landing-data {
    position: absolute;
    color: #ffffff;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;
    align-items: center;
    top: 0px;
    min-height: 100vh;
    width: 100vw;
}

.col-12 {
    width: 99.99%;
}

.iia,
.head,
.vit {
    display: block;
    text-transform: uppercase;
}

.iia {
    margin-bottom: 1rem;
    font-size: 3vw;
    font-weight: 500;
}

.head {
    font-size: 5vw;
    font-weight: 700;
    overflow-x: hidden;
}

.vit {
    font-size: 4vw;
    font-weight: 600;
}

.btn-know {
    margin-top: 4rem;
    background-color: #f6753d;
    color: #000000;
    border: none;
    border-radius: 40px;
    padding: 15px 35px;
    font-size: 1.6rem;
    font-weight: 700;
}

.scrolldown {
    text-transform: uppercase;
    transform: rotate(90deg);
    position: absolute;
    color: aliceFD6420;
    font-weight: 600;
    bottom: 70px;
    right: -30px;
}

@media (max-width: 991px) {
    .head {
        font-size: 6vw;
    }
    .vit {
        font-size: 5vw;
    }
    .btn-know {
        margin-top: 4rem;
        border-radius: 30px;
        padding: 15px 15px;
        font-size: 1.2rem;
    }
}

@media (max-width: 600px) {
    .iia {
        font-size: 4vw;
    }
    .head {
        font-size: 9vw;
    }
    .side-icons {
        position: absolute;
        top: 85vh;
        left: auto;
    }
    .side-icons span {
        display: inline-block;
        width: 20px;
        margin: 15px 10px;
    }
}

@media (max-width: 400px) {
    .iia {
        font-size: 9vw;
    }
    .head {
        font-size: 14vw;
    }
    .vit {
        font-size: 7vw;
    }
}

.footer {
    background-color: #000000;
    text-align: center;
    padding: 60px;
}

.footer a {
    color: #ffffff;
    font-size: 1.5vw;
}

.footer img {
    height: 5vw;
}

.footer .iia {
    color: #f6753d;
    font-size: 2vw;
    margin: 1.3rem 0;
}

@media (max-width: 900px) {
    .footer img {
        height: 10vw;
    }
    .footer .iia {
        font-size: 3vw;
    }
    .footer a {
        font-size: 2.8vw;
    }
}

@media (max-width: 600px) {
    .footer img {
        height: 14vw;
    }
    .footer .iia {
        font-size: 4vw;
    }
    .footer a {
        font-size: 3.8vw;
    }
}

.top {
    position: absolute;
    top: 100px;
    left: 26px;
}

.top a {
    display: block;
    padding: 8px;
    font-size: 22px;
    margin: 15px 2px;
    width: 24px;
    height: 24px;
}

.bottom {
    margin-top: 10px;
}

.bottom a {
    display: inline-block;
    width: 24px;
    height: 24px;
    padding: 8px;
    font-size: 22px !important;
    margin: 10px 7px;
}

.top a,
.bottom a {
    background-color: #f6753d;
    text-decoration: none;
    color: #ffffff;
    border-radius: 50%;
}

@media (max-width: 600px) {
    .top {
        position: absolute;
        top: 85vh;
        left: auto;
    }
    .top a {
        display: inline-block;
        width: 6vw;
        height: 6vw;
        padding: 4px;
        font-size: 5vw;
        margin: 15px 10px;
    }
    .bottom a {
        display: inline-block;
        background-color: #f6753d;
        width: 6vw;
        height: 6vw;
        padding: 4px;
        font-size: 5vw !important;
        text-align: center;
        color: #ffffff;
        margin: 10px 5px;
        border-radius: 50%;
    }
}

.topnav {
    position: fixed;
    top: 0;
    z-index: 10;
    width: calc(100vw - 52px);
    overflow: hidden;
    padding: 15px 26px;
    transition: all 0.3s ease-in;
    opacity: 1;
}

.scrolled {
    opacity: 1;
    background-color: #051640fe;
}

.topnav img {
    float: left;
}

.topnav a {
    float: right;
    display: block;
    color: #ffffff;
    text-align: center;
    padding: 10px 15px;
    text-decoration: none;
    font-size: 20px;
    text-transform: uppercase;
}

.topnav a:hover {
    text-shadow: 0 0 1px #eee;
}

.topnav .icon {
    display: none;
}

.sidenav a {
    display: none;
}

@media screen and (max-width: 768px) {
    .topnav a {
        display: none;
    }
    .topnav .icon {
        float: right;
        display: block;
        background-color: transparent;
        border: none;
        color: #fff;
        font-size: 24px;
        padding: 10px;
    }
    .topnav .icon:focus {
        outline: none;
    }
    .sidenav {
        background-color: #0006;
    }
    .sidenav a {
        display: block;
        float: left;
        width: 90%;
    }
}

.loading-screen {
    display: flex;
    position: fixed;
    justify-content: center;
    align-items: center;
    left: 0;
    opacity: 1;
    z-index: 100;
    background-color: #5d3f9b;
    height: 100%;
    width: 100vw;
    transition: all 1s ease-in-out;
}

.moved {
    left: 100%;
    opacity: 0.9;
}

@keyframes fade {
    0% {
        left: 0;
        opacity: 1;
    }
    50% {
        left: 0;
        opacity: 1;
    }
    100% {
        left: 100%;
        opacity: 0.9;
    }
}


