.g_imgs {
    margin: 20px;
    width: 28%;
    overflow: hidden;
    position: relative;
    transition: all 0.1s ease-in;
}

.g_imgs img {
    width: 100%;
    height: 200px;
    object-fit: cover;
    transition: all 0.3s ease-in-out;
}

.g_imgs .img-overlay {
    position: absolute;
    display: flex;
    align-items: center;
    bottom: 100%;
    left: 0;
    right: 0;
    background-color: #8141fa33;
    color: #ffffff;
    overflow: hidden;
    width: 100%;
    height: 0;
    transition: 0.5s ease;
}

.g_imgs:hover .img-overlay {
    bottom: 0;
    height: 100%;
}

.g_imgs:hover {
    box-shadow: 0 0 20px #051640;
}

.g_imgs:hover img {
    transform: scale(1.3) rotate(5deg);
}

